.popup-options {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  max-width: 810px;
  height: 100%;
  max-height: 515px;
  background: #3a3e52;
  z-index: 99;
  padding-top: 80px;
  padding-bottom: 60px;
  display: none;
  transform-origin: center;
  .wrap__popup {
    max-width: 580px;
    height: 100%;
    max-height: 370px;
    color: #000;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column; } }


.popup-options {
  .wrap__exit-popup {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    button {
      background-color: transparent;
      width: 28px;
      height: 28px;
      position: absolute;
      top: 15px;
      right: 25px; } }

  .popup__btn {
    display: block;
    position: absolute;
    border-radius: 2px;
    top: 20px;
    right: 0px;
    width: 100%;
    height: 3px;
    background-color: #fff; }

  .popup__btn-left {
    transform: rotate(45deg); }
  .popup__btn-right {
    transform: rotate(-45deg); } }



.popup-options-title {
  font-size: 24px;
  font-weight: 600;
  letter-spacing: 0.48px;
  color: #ffffff; }

.popup-options-subtitle {
  font-size: 16px;
  font-weight: normal;
  letter-spacing: 0.32px;
  color: #ffffff;
  padding-top: 50px;
  padding-bottom: 40px; }

.popup-options-links {
  font-size: 16px;
  font-weight: normal;
  letter-spacing: 0.32px;
  a {
    color: #4f48f0; }
  a:first-child {
    margin-right: 55px; } }

.popup-options-links-subtext {
  font-size: 16px;
  font-weight: normal;
  letter-spacing: 0.32px;
  color: #ffffff;
  padding-top: 50px;
  padding-bottom: 60px; }

.popup-options-btn {
  display: flex;
  align-items: center;
  button {
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 0.32px;
    color: #ffffff;
    padding: 12px 25px;
    border-radius: 5px;
    background-color: #2c2e3e;
    margin-right: 50px; }
  .text-button-right {
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 0.32px;
    color: #ffffff; } }
