.popup-content {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  max-width: 1000px;
  height: 100%;
  max-height: 930px;
  background: #3a3e52;
  z-index: 99;
  padding-top: 80px;
  padding-bottom: 55px;
  margin: 0 15px;
  display: none;
  transform-origin: center;
  @include r(1480) {
    max-width: 1200px;
    @include r(1330) {
      max-width: 1100px; } }



  .wrap__popup-img {
    max-width: 890px;
    max-height: 800px;
    overflow: scroll;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover; } }






  .wrap__exit-popup {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    button {
      background-color: transparent;
      width: 35px;
      height: 35px;
      position: absolute;
      top: 15px;
      right: 15px; } }

  .popup__btn {
    display: block;
    position: absolute;
    border-radius: 2px;
    top: 20px;
    right: 0px;
    width: 100%;
    height: 3px;
    background-color: #fff; }

  .popup__btn-left {
    transform: rotate(45deg); }
  .popup__btn-right {
    transform: rotate(-45deg); } }
