.popup-scan {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  max-width: 666px;
  height: 100%;
  max-height: 340px;
  background: #3a3e52;
  z-index: 99;
  padding-top: 110px;
  padding-bottom: 90px;
  display: none;
  transform-origin: center;

  .wrap__popup {
    max-width: 470px;
    height: 100%;
    max-height: 160px;
    color: #000;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column; } }

.popup-scan-container {
  display: flex; }

.scan-text {
  margin-left: 75px; }

.scan-img {
  display: flex;
  align-items: center; }

.scan-text-title {
  font-size: 24px;
  font-weight: 600;
  letter-spacing: 0.48px;
  color: #ffffff;
  padding-bottom: 45px; }

.scan-text-subtitle {
  font-size: 16px;
  font-weight: normal;
  letter-spacing: 0.32px;
  color: #ffffff; }

.popup-scan {
  .wrap__exit-popup {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    button {
      background-color: transparent;
      width: 28px;
      height: 28px;
      position: absolute;
      top: 15px;
      right: 25px; } }

  .popup__btn {
    display: block;
    position: absolute;
    border-radius: 2px;
    top: 20px;
    right: 0px;
    width: 100%;
    height: 3px;
    background-color: #fff; }

  .popup__btn-left {
    transform: rotate(45deg); }
  .popup__btn-right {
    transform: rotate(-45deg); } }
