.popup-envelope {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  max-width: 1435px;
  height: 100%;
  max-height: 706px;
  background: #3a3e52;
  z-index: 99;
  padding-top: 55px;
  padding-bottom: 55px;
  margin: 0 15px;
  display: none;
  transform-origin: center;
  @include r(1480) {
    max-width: 1200px;
    @include r(1330) {
      max-width: 1100px; } } }



.wrap__popup-img {
  max-width: 1200px;
  max-height: 590px;
  @include r(1480) {
    max-width: 1000px;
    @include r(1330) {
      max-width: 900px; } }
  img {
    width: 100%;
    height: 100%;
    object-fit: cover; } }
